
import { defineComponent, computed } from 'vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import takeCourseHistory from '@/graphql/queries/takeCourseHistry'
import { TakeCourseHistoryQuery } from '@/graphql/@types/types'
import Header from '@/components/Header.vue'
import NoItem from '@/components/NoItem.vue'
import { faHistory } from '@fortawesome/free-solid-svg-icons'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import { useStore } from 'vuex'

export default defineComponent({
  components: {
    Header,
    NoItem
  },
  setup() {
    const store = useStore()
    const uid = store.state.currentUser?.uid

    const { result, refetch } = useQuery<TakeCourseHistoryQuery>(takeCourseHistory, {
      student_id: uid
    })
    refetch()
    const courses = useResult(result, null, (data) => data.SubscriptionCourses)

    const searchIcon = computed(() => faSearch)
    const historyIcon = computed(() => faHistory)

    return {
      searchIcon,
      historyIcon,
      courses
    }
  }
})
