import { gql } from '@apollo/client/core'

const takeCourseHistory = gql`
  query TakeCourseHistory($student_id: uuid) {
    SubscriptionCourses(where: { student_id: { _eq: $student_id } }) {
      id
      last
      Course {
        image
        title
        Instructor {
          name
        }
      }
    }
  }
`

export default takeCourseHistory
