<template>
  <Header :title="$t('take-course-list.title')" :is-back-button="false" />
  <NoItem :text="$t('take-course-list.title')" v-if="!courses?.length">
    <font-awesome-icon :icon="historyIcon" />
  </NoItem>
  <div class="wrap" v-else>
    <div class="search-wrap">
      <form class="search-form">
        <label>
          <input type="text" class="search-input" :placeholder="$t('take-course-list.input')" />
          <font-awesome-icon :icon="searchIcon" class="icon" />
        </label>
        <button type="submit" class=""></button>
      </form>
    </div>

    <div class="course-wrap">
      <router-link
        v-for="(course, index) in courses"
        :key="index"
        :to="{ name: 'TakeCourseDetail', query: { id: course.id } }"
        class="course-list"
      >
        <img :src="course.Course.image" alt="" class="image" />
        <div class="course-info">
          <p class="name">{{ course.Course.Instructor.name }}</p>
          <p class="title">{{ course.Course.title }}</p>
          <progress max="100" :value="course.last" class="progress-bar" />
          <p class="progress-value">{{ course.last }}% {{ $t('take-course-list.finished') }}</p>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import takeCourseHistory from '@/graphql/queries/takeCourseHistry'
import { TakeCourseHistoryQuery } from '@/graphql/@types/types'
import Header from '@/components/Header.vue'
import NoItem from '@/components/NoItem.vue'
import { faHistory } from '@fortawesome/free-solid-svg-icons'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import { useStore } from 'vuex'

export default defineComponent({
  components: {
    Header,
    NoItem
  },
  setup() {
    const store = useStore()
    const uid = store.state.currentUser?.uid

    const { result, refetch } = useQuery<TakeCourseHistoryQuery>(takeCourseHistory, {
      student_id: uid
    })
    refetch()
    const courses = useResult(result, null, (data) => data.SubscriptionCourses)

    const searchIcon = computed(() => faSearch)
    const historyIcon = computed(() => faHistory)

    return {
      searchIcon,
      historyIcon,
      courses
    }
  }
})
</script>

